@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.servicios {
    background-color: v.$fondoDos;
    padding: 2rem 0;
    box-shadow: 0 0 3.2rem -0.8rem v.$secundario;
    &__h2-servicios {
        color: v.$letras;
        margin: 0 auto 2rem auto;
        width: 20rem;
        font-size: 2.6rem;
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
    }
    &__grid {
        width: 90%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 5rem;
        justify-content: space-around;
    }
    &__planes {
        display: flex;
        flex-direction: column;
        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }
        gap: 3rem;
    }
    &__planes-box {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        gap: 2rem;


        padding: 2rem 0;
        background-color: v.$fondo;
        border: 2px solid v.$fondo;
        transition-property: border;
        transition-duration: .3s;
        &:hover {
            border-color: v.$letrasTitulo;
        }
        border-radius: 2rem;
        cursor: pointer;
    }
    &__divisor {
        background-color: v.$secundario;
        width: 80%;
        height: 2px;
        margin: 0 auto;
    }
    &__h3-servicios {
        padding: 0;
        margin: 0;
        text-transform: capitalize;
    }
    &__detalles {
        padding: 2rem 0;
        margin: 0 auto;
        font-size: 1.6rem;
        color:darken(v.$secundario, 30);
        text-align: center;
        width: 90%;

    }
    &__contenido {
        padding: 0.3rem;
        margin: 0 auto;
        width: 60%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

    }
    &__opcion {
        padding: 0;
        margin: 0;
        font-size: 1.4rem;

    }
    &__cantidad {
        padding: 4px 1.4rem 1px 1.4rem;
        margin: 0;
        text-align: center;
        color: v.$letras;
        background-color: lighten(v.$letrasTitulo,30);
        border-radius: 2rem;
        font-size: 1.4rem;
        font-weight: 700;
    }
    &__precio {
        padding: 0;
        margin: 0;
        font-size: 3.6rem;
        font-weight: 900;
        color: v.$letrasTitulo;
    }
    &__dolar {
        margin: 0;
        padding: 0;
        font-size: 2rem;
    }
    &__descuento {
        display: block;
        margin: 0;
        padding: 0;
        font-size: 1.3rem;
        color: darken(v.$secundario, 40);
    }
    &__personalizar-box {
        text-align: center;
    }
    &__aqui {
        color: v.$letrasTitulo;
        &:hover {
            cursor: pointer;
        }
    }
}