@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.bienvenida {
    @include m.bgImage( "../img/fondo-bienvenida.webp", v.$primarioOpacity);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 60rem;
    &__principal-box {
        background-color: lighten(v.$primario, 3);
        margin-top: 14rem;
        padding: 1rem;
        padding-bottom: 3rem;
        border-radius: 12px 12px 0 0;
    }
    &__fotoperfil-borde {
        margin: -17rem auto 0 auto;
        @include m.tablet {
            width: 34rem;
            height: 34rem;
        }
        border: 2rem solid lighten(v.$negro, 3);
        border-radius: 100%;
    }
    &__fotoperfil-principal {
        background-color: v.$secundario;
        background-image: url("../img/foto_perfil.webp");
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center -1rem;
        width: 28rem;
        height: 28rem;
        @include m.tablet {
            width: 30rem;
            height: 30rem;
        }
        border: 2px solid v.$letrasTitulo;
        border-radius: 100%;
    }

    &__titulo-bienvenida {
        width: 90%;
        margin: 0 auto;
    }
    &__nombre-h2 {
        text-align: center;
        font-size: 2.8rem;
        color: v.$letrasDos;
        text-transform: capitalize;
        margin: 0 auto 2rem auto;
    }
    &__ocupacion {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        text-transform: capitalize;
        color: v.$secundario;
    }
    &__navegacion {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 80%;
        margin: 0 auto;
        padding: 0.6rem;
        border-radius: 0.5rem;
        background-color: lighten(v.$primario, 1);
    }
    &__link-bienvenida {
        text-decoration: none;
    }
    &__redsocial-box {
        padding: 0.6rem;
        border-radius: 100%;
    }
    &__redsocial-img {
        width: 2rem;
        height: 2rem;
    }
}