@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.habilidades {
    // background-color: v.$primario;
    @include m.bgImage( "../img/fondo-habilidades.webp", v.$primarioOpacity);
    margin-top: -2px;
    &__panel {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
        padding: 6rem 0;
    }
    &__titulo-h2 {
        margin: 0;
        padding: 0;
        padding-bottom: 2rem;
        color: v.$letrasDos;
    }
    &__resumen {
        margin: 0;
        padding: 0;
        color: darken(v.$secundario, 30);
        text-align: justify;
    }
    &__grid {
        margin: 2rem 0;
        padding: 0;
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
        @include m.tablet {        
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: start;
        }
        gap: 4rem;
    }
    &__tecnicas {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__titulo-h3 {
        margin: 0;
        text-align: center;
        color: v.$letrasDos;
        padding: 1rem 0 2rem 0;
        text-transform: capitalize;
        font-size: 2.4rem;
        @include m.tablet {
            text-align: start;
        }
    }


    &__profesionales {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__tecnicas-panel,
    &__profesionales-panel {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        margin-bottom: 1rem;
    }
    &__nombre {
        color: v.$fondoDos;
        display: block;
        padding: 0;
        margin: 0;
        font-size: 1.8rem;
        font-weight: 400;
    }
}

.barra {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    margin-top: 0.5rem;
    padding: 0.4rem;
    border-radius: 4px;
    background-color: lighten(v.$primario, 10);
    &__porcentaje {
        padding: 0.4rem;
        color: v.$letrasTitulo;
        font-weight: 700;
        font-size: 1.5rem;
        background-color: lighten(v.$primario, 10);
    }
    &__inicial {
        width: 75%;
        margin: 0;
        padding: 0;
        background-color: v.$letrasTitulo;
    }
    &__intermedia {
        width: 4px;
        margin: 0;
        padding: 0;
        background-color: lighten(v.$primario, 10);
    }
    &__final {
        width: 25%;
        margin: 0;
        padding: 0;
        background-color: lighten(v.$letrasTitulo, 30);
    }
}