@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.header {
    background-color: lighten(v.$primario, 1);
    padding: 0rem;
    margin: 0;
    &__grid {
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            padding: 0 4rem;
        }
    }
    &__titulo-h1 {
        color: v.$letrasTitulo;
        font-size: 3rem;
        text-transform: capitalize;
    }
    &__nav {
        display: none;
        @include m.tablet {
            padding: 0.6rem 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            gap: 5rem;
        }
    }
    &__link {
        text-transform: uppercase;
        color: v.$letrasDos;
        font-size: 1.6rem;
        font-weight: 700;
        padding: 0.6rem;
        transition-property: border;
        transition-duration: .3s;
        border-top: 4px solid v.$primario;
        border-bottom: 4px solid v.$primario;
        &:hover {
            border-bottom-color: v.$fondo;
        }
    }
    &__img {
        &__perfil {
            background-color: v.$secundario;
            background-image: url("../img/foto_perfil.webp");
            background-repeat: no-repeat;
            background-size: 100%;
            background-position: center center;
            width: 4rem;
            height: 4rem;
            border: 4px solid v.$secundario;
            border-radius: 100%;
            order: -1;
            @include m.tablet {
                order: 0;
            }
        }
    }
}