@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.footer {
    background-color: v.$primario;
    padding: 1rem 1rem 3rem 1rem;
    &__panel {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 2rem;
    }
    &__redes {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 80%;
        margin: 0 auto;
        padding: 0.6rem;
        border-radius: 0.5rem;
        background-color: lighten(v.$primario, 1);
    }
    &__linea-horizontal {
        @include m.tablet {
            width: 40%;
            height: 2px;
            background-color: lighten(v.$letrasTitulo, 20);
            display: inline-block;
        }
    }
    &__link-footer {
        text-decoration: none;
    }
    &__redsocial-box {
        padding: 0.6rem;
        border-radius: 100%;
    }
    &__redsocial-img {
        width: 2rem;
        height: 2rem;
    }

    &__titulo-footer {
        width: 90%;
        margin: 0 auto;
    }
    &__nombre-h2 {
        text-align: center;
        font-size: 2.8rem;
        color: v.$letrasTitulo;
        text-transform: capitalize;
        margin: 0 auto 2rem auto;
    }
    &__ocupacion {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        text-transform: capitalize;
        color: v.$secundario;
    }

    &__listado {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        margin: 0 auto;
    }
    &__link-listado {
        text-decoration: none;
        color: v.$letrasDos;
        font-size: 2rem;
    }
    &__separacion-vertical {
        width: 2px;
        height: 3rem;
        display: inline-block;
        background-color: lighten(v.$letrasTitulo, 20);
    }
}