@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.sobremi {
    background-color: v.$primario;
    margin-top: -2px;
    &__panel {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
        padding: 6rem 0;
    }
    &__titulo-h2 {
        margin: 0;
        padding: 0;
        padding-bottom: 2rem;
        color: v.$letrasDos;
    }
    &__resumen {
        margin: 0;
        padding: 0;
        color: darken(v.$secundario, 30);
        text-align: justify;
    }
    &__span-hola {
        color: v.$letrasTitulo;
    }
    &__grid {
        margin: 2rem 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {        
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: start;
        }
        gap: 4rem;
    }
    &__contacto {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__titulo-h3 {
        margin: 0;
        text-align: center;
        color: v.$letrasDos;
        padding: 1rem 0 2rem 0;
        text-transform: capitalize;
        font-size: 2.4rem;
        @include m.tablet {
            text-align: start;
        }
    }
    &__datos-box {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        justify-content: space-around;
    }
    &__dato {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
    }
    &__opcion-dato {
        margin: 0;
        padding: 0;
        color: v.$letrasTitulo;
        font-size: 1.8rem;
    }
    &__detalle {
        color: v.$letrasDos;
        text-transform: capitalize;
        font-size: 1.8rem;
    }
    &__interes {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__interes-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        @include m.tablet {
            grid-template-columns: repeat(3, 1fr);
        }
        gap: 2rem;
        justify-content: space-around;
    }
    &__interes-box {
        background-color: lighten(v.$primario, 10);
        padding: 1rem;
        border-radius: 4px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        gap: 2rem;
    }
    &__interes-icons {
        padding: 0;
        font-weight: 700;
        color: v.$letrasTitulo;
    }
    &__interes-icons-nombre {
        font-weight: 700;
        color: v.$secundario;
    }
}